.footer-outer {
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 48px 0;
    background-color: #e6e6fa;
  }
  .footer-copy {
    width: 100%;
    text-align: center;
    font-size: 14px;
    margin-top: 30px;
    font-family: 'Poppins', sans-serif;
    color: #333;
  }
  
  .footer-container {
    width: 1296px;
    padding: 72px;
    background: white;
    border-radius: 24px;
    outline: 8px solid #662D91;
    outline-offset: -8px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 32px;
    flex-wrap: wrap;
  }
  
  /* Mobile Responsive Layout */
  @media (max-width: 768px) {
    .footer-container {
      flex-direction: column;
      width: 90%;
      padding: 32px 24px;
      align-items: center;
      text-align: center;
    }
  
    .footer-brand {
      width: 100%;
      align-items: center;
      justify-content: center;
      display: flex;
    }
  
    .footer-logo {
      width: auto;
    }
  
    .footer-section {
      width: 100%;
      max-width: none;
      margin-top: 24px;
    }
  }
  
  .footer-brand {
    width: 405px;
    display: flex;
    flex-direction: column;
    justify-content: left;
    align-items: flex-start;
  }
  
  .footer-heading {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;
  }
  
  .footer-logo {
    font-family: Arial;
    font-size: 40px;
    line-height: 32.76px;
  }
  
  .purple-text {
    color: #662D91;
    font-weight: 900;
  }
  
  .black-text {
    color: black;
    font-weight: 400;
  }
  
  .footer-subtext {
    letter-spacing: 15.2px;
    font-size: 40px;
    font-family: Arial;
    font-weight: 400;
    color: black;
    line-height: 28.11px;
  }
  
  .footer-description {
    color: #0F0F0F;
    font-size: 16px;
    font-family: Arial;
    font-weight: 400;
  }
  
  .footer-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;
    max-width: 200px;
  }
  
  .footer-title {
    color: #662D91;
    font-size: 24px;
    font-family: Arial;
    font-weight: 700;
  }
  
  .footer-links {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    color: #0F0F0F;
    font-size: 20px;
    font-family: Arial;
    font-weight: 400;
  }
  