/* Wrapper */
.hero-wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }
  
  /* Hero Main Section */
  .hero-section {
    padding-top:36px;
    position: relative;
    height: 720px;
    width: 100%;
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.20), rgba(255, 255, 255, 0.20)), #E6E6FA;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    overflow: hidden;
  }
  
  /* Left Images */
  .hero-left-images {
    position: absolute;
    top: -79px;
    left: -77px;
    width: 338px;
    height: 879px;
    pointer-events: none;
  }
  
  .img-left-1,
  .img-left-2,
  .img-left-3 {
    position: absolute;
  }
  
  .img-left-1 {
    top: 104px;
    left: 0;
    width: 256px;
    height: 256px;
    transform: rotate(-24deg);
  }
  
  .img-left-2 {
    top: 615px;
    left: 63px;
    width: 226px;
    height: 226px;
    transform: rotate(11deg);
  }
  
  .img-left-3 {
    top: 371px;
    left: 21px;
    width: 211px;
    height: 211px;
  }
  
  /* Right Images */
  .hero-right-images {
    position: absolute;
    top: 0;
    right: 0;
    width: 400px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
    padding: 40px 20px;
    pointer-events: none;
  }
  
  .img-right-1, .img-right-2, .img-right-3 {
    width: auto;
    max-width: 100%;
    height: auto;
    margin-bottom: 30px;
    transform: none; /* Avoid unnecessary rotations for now */
  }
  
