  /* Glass Content Box */
  .hero-content {
    margin-top:1000px;
    position: relative;
    margin: 0 72px;
    height: 640px;
    padding: 60px;
    background: rgba(255, 255, 255, 0.20);
    backdrop-filter: blur(18px);
    border-radius: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: auto; /* optional, auto spacing between text and image */
    overflow: hidden;
  }
  
  
  /* Text Side */
  .hero-text {
    max-width: 498px;
    display: flex;
    flex-direction: column;
    gap: 36px;
  }
  
  .hero-text h1 {
    font-size: 48px;
    font-family: Inter, sans-serif;
    font-weight: 400;
    color: #000;
    line-height: 1.3;
  }
  
  .highlight {
    color: #5936BC;
    font-weight: 700;
  }
  
  .hero-text p {
    font-size: 20px;
    font-family: Inter, sans-serif;
    font-weight: 400;
    color: #000;
    line-height: 1.6;
  }
  
  .hero-button {
    padding: 12px 20px;
    background: #5936BC;
    color: white;
    font-size: 24px;
    font-weight: 600;
    font-family: Inter, sans-serif;
    border-radius: 10px;
    display: inline-block;
    cursor: pointer;
    transition: background 0.3s ease;
  }
  
  .hero-button:hover {
    background: #472ca0;
  }
  
  /* Image Side */
  .hero-main-img {
    width: 592px;
    height: 592px;
    position: relative;
    z-index: 1;
  }
  
  /* CTA Bar */
  .hero-cta {
    background: #5936BC;
    padding: 36px 10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .hero-cta div {
    font-size: 48px;
    color: #E6E6FA;
    font-family: Poppins, sans-serif;
    font-weight: 700;
    text-align: center;
  }
  @media (max-width: 768px) {
    .hero-wrapper {
      overflow-x: hidden;
    }
  
    .hero-section {
      height: auto;
      padding: 24px 16px;
    }
  
    .hero-left-images,
    .hero-right-images {
      display: none; /* Hide floating decor images on small screens */
    }
  
    .hero-content {
      flex-direction: column;
      align-items: center;
      text-align: center;
      height: auto;
      margin: 0 16px;
      padding: 40px 20px;
      gap: 32px;
    }
  
    .hero-text {
      max-width: 100%;
      align-items: center;
      gap: 24px;
    }
  
    .hero-text h1 {
      font-size: 32px;
    }
  
    .hero-text p {
      font-size: 16px;
    }
  
    .hero-button {
      font-size: 18px;
      padding: 10px 16px;
    }
  
    .hero-main-img {
      width: 100%;
      max-width: 320px;
      height: auto;
    }
  
    .hero-cta {
      padding: 24px 10px;
    }
  
    .hero-cta div {
      font-size: 24px;
    }
  }
/* Testimonials Section Wrapper */
.testimonials-wrapper {
    padding: 72px;
    background: transparent; /* or remove this if you want natural page background */
    box-sizing: border-box;
    background-color: #e6e6fa;
    width: 100%;
  }

.testimonials-section {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: 40px 0;
  }
  
  /* Shared Testimonial Styles */
  .testimonial {
    display: flex;
    align-items: center;
    gap: 32px;
    padding: 40px;
    background-color: #000;
  }
  
  .testimonial-1 {
    border-top-left-radius: 48px;
    border-top-right-radius: 48px;
  }
  
  .testimonial-2 {
    border-bottom-left-radius: 48px;
    border-bottom-right-radius: 48px;
  }
  
  .testimonial-img {
    width: 300px;
    height: 340px;
    border-radius: 32px;
    object-fit: cover;
  }
  
  .testimonial-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 24px;
  }
  
  .testimonial-text {
    color: #fff;
    font-family: Poppins, sans-serif;
    font-size: clamp(18px, 2vw, 24px);
    line-height: 1.5;
  }
  
  .testimonial-author {
    font-family: Poppins, sans-serif;
    font-size: clamp(20px, 2.5vw, 28px);
    color: #5936BC;
  }
  
  .author-name {
    font-weight: 700;
  }
  
  .author-role {
    font-weight: 400;
  }
  
  /* Middle CTA Block */
  .testimonial-cta {
    background-color: #5936BC;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 36px 72px;
    gap: 24px;
  }
  
  .testimonial-cta-text {
    color: #E6E6FA;
    font-family: Poppins, sans-serif;
    font-size: clamp(20px, 2.2vw, 28px);
    text-align: center;
    max-width: 900px;
  }
  
  .testimonial-btn {
    padding: 16px 32px;
    background-color: #E6E6FA;
    color: #5936BC;
    font-family: Inter, sans-serif;
    font-size: 18px;
    font-weight: 600;
    border: none;
    border-radius: 3000px;
    cursor: pointer;
  }
  
  /* Responsive */
  @media (max-width: 768px) {
    .testimonials-wrapper {
        padding: 24px;
      }
    .testimonial,
    .testimonial-cta {
      flex-direction: column;
      text-align: center;
      gap: 20px;
      padding: 24px;
    }
  
    .testimonial-img {
      width: 100%;
      max-width: 280px;
      height: auto;
      margin: 0 auto;
    }
  
    .testimonial-cta {
      padding: 24px;
    }
  
    .testimonial-btn {
      width: 100%;
      max-width: 280px;
      padding: 14px;
      font-size: 16px;
    }
  }
  /* Wrapper for full screen and padding */
.landing-wrapper {
    width: 100%;
    background: #E6E6FA;
    padding: 48px 24px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
  }
  
  /* Main container layout */
  .landing-container {
    max-width: 1280px;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 48px;
    flex-wrap: wrap;
  }
  
  /* Image styling */
  .landing-image {
    width: 100%;
    max-width: 600px;
    height: auto;
    object-fit: cover;
  }
  
  /* Content section */
  .landing-content {
    flex: 1;
    max-width: 600px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 24px;
    text-align: center;
  }
  
  /* Title formatting */
  .landing-title {
    font-size: 2.5rem;
    font-family: 'Poppins', sans-serif;
    line-height: 1.2;
  }
  
  .regular-text {
    color: #0F0F0F;
    font-weight: 400;
  }
  
  .highlight-text {
    color: #5936BC;
    font-weight: 700;
  }
  
  /* Description formatting */
  .landing-description {
    font-size: 1.25rem;
    font-family: 'Poppins', sans-serif;
    color: #0F0F0F;
  }
  
  /* Button styles */
  .landing-button {
    padding: 12px 20px;
    background: #5936BC;
    box-shadow: inset 8px 8px 8px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    align-self: center;
    transition: transform 0.2s ease;
  }
  
  .landing-button:hover {
    transform: scale(1.05);
  }
  
  .button-text {
    color: white;
    font-size: 1.25rem;
    font-family: 'Inter', sans-serif;
    font-weight: 600;
  }
  
  /* Responsive Styles */
  @media screen and (max-width: 768px) {
    .landing-container {
      flex-direction: column;
      align-items: center;
      gap: 32px;
    }
  
    .landing-title {
      font-size: 2rem;
    }
  
    .landing-description {
      font-size: 1rem;
    }
  
    .button-text {
      font-size: 1rem;
    }
  }
  

