.login-page {
    height: 100vh !important;
    width: 100vw !important;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  }
  .login-page h1 {
  }
  .login-form {
    background-color: #b9bcccb6;
    margin-left: -15px;
    padding: 30px;
  }
  
  .login-form h1 {
    text-align: center;
    font-weight: bold;
    margin: 30px;
    color: #344265;
  }
  
  .login-form .ant-form-item-label label {
    color: #344265 !important;
  }
  .login-form button {
    background-color: #344265;
    color: white;
    outline: none;
  }
  .login-form button:hover {
    background-color: black;
    color: white;
  }
  .login-form a {
    color: #344265 !important;
  }
  
  .login-form a:hover {
    color: black !important;
  }
  .btn{
    background-color: #662d91;
    border-color: #fff;
}
.btn:hover{
    background-color: #4B0082;
    border-color: white;
}
.logo{
  width:300px;
  padding-bottom: 40px;
}
.form-container{
  padding: 48px;
  background: #E6E6FA;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
  box-shadow: 6px 6px 6px 6px rgb(0 0 0 / 20%);
}
.login-pic{
    display: inline-flex;
    padding: 40px;
    gap: 16px;
    background-color: #662d91;
    border-radius: 50px;
    box-shadow: inset 0 0 12px rgba(0, 0, 0, 0.2); /* equal inset shadow */
  }

.Image{
  height:606px;
  width:700px;
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
  box-shadow: 6px 6px 6px 6px rgb(0 0 0 / 20%);
}
@media (max-width: 768px) {
  .login-pic {
    flex-direction: column;
    padding: 20px;
    border-radius: 30px;
    align-items: center;
  }

  .form-container {
    padding: 24px;
    width: 100%;
    border-radius: 30px;
    border-top-right-radius: 0;
    border-bottom-left-radius: 0;
  }

  .Image {
    width: 100%;
    height: auto;
    border-radius: 30px;
    border-top-left-radius: 0;
    border-bottom-right-radius: 0;
    margin-top: 20px;
  }

  .logo {
    width: 200px;
    padding-bottom: 20px;
  }

  .register-page {
    padding: 20px;
  }
}

@media (max-width: 480px) {
  .form-container {
    padding: 16px;
  }

  .form-container h1 {
    font-size: 1.25rem;
  }

  .btn {
    width: 100%;
    margin-top: 10px;
  }

  .Image {
    display: none; /* Optional: hide image on small screens */
  }
}

