
.register-page {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url('../assets/LoginBG.png');
  background-size: 100%;
}
.register-form {
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 20px;
  width: 30%;
}

.register-form h2 {
  margin-top: 10px;
  text-align: center;
  font-weight: bold;
  color: #b87333;
}

.register-form a {
  color: #b87333 !important;
}

.register-form a:hover {
  color: black !important;
}

.register-form button {
  background-color: #b87333;
  color: white;
}

.register-form input:focus {
  border: 1px solid #b87333 !important;
  color: #b87333;
}
.register-form button:hover {
  background-color: black;
  color: white;
}
.btn{
    background-color: #662d91;
    border-color: #fff;
}
.btn:hover{
    background-color: #4B0082;
    border-color: white;
}
.logo{
  width:300px;
  padding-bottom: 40px;
}
.form-container{
  padding: 48px;
  background: #E6E6FA;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
  box-shadow: 6px 6px 6px 6px rgb(0 0 0 / 20%);
}
.login-pic{
  display: inline-flex;
  padding:40px;
  gap: 16px;
  background-color: #662d91;
  border-radius: 50px;
  box-shadow: 6px 6px 6px 6px rgb(0 0 0 / 20%) inset;
}
.Image{
  height:606px;
  width:700px;
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
  box-shadow: 6px 6px 6px 6px rgb(0 0 0 / 20%);
}
@media (max-width: 768px) {
  .login-pic {
    flex-direction: column;
    padding: 20px;
    border-radius: 30px;
    align-items: center;
  }

  .form-container {
    padding: 24px;
    width: 100%;
    border-radius: 30px;
    border-top-right-radius: 0;
    border-bottom-left-radius: 0;
  }

  .Image {
    width: 100%;
    height: auto;
    border-radius: 30px;
    border-top-left-radius: 0;
    border-bottom-right-radius: 0;
    margin-top: 20px;
  }

  .logo {
    width: 200px;
    padding-bottom: 20px;
  }

  .register-page {
    padding: 20px;
  }
}

@media (max-width: 480px) {
  .form-container {
    padding: 16px;
  }

  .register-form h2,
  .form-container h1 {
    font-size: 1.25rem;
  }

  .btn {
    width: 100%;
    margin-top: 10px;
  }

  .Image {
    display: none; /* Optional: Hide image on very small screens */
  }
}
