.navbar {
  width: 100%;
  padding: 16px 60px;
  background: #e6e6fa;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  vertical-align: middle;
}

.navbar-logo {
  width: 201.51px;
  height: 36px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 7.48px;
}

.navbar-title {
  width: 100%;
  text-align: center;
}

.logo-primary {
  color: #662D91;
  font-size: 18.7px;
  font-family: Arial, sans-serif;
  font-weight: 900;
  line-height: 15.32px;
  text-decoration: none;
  margin-right: 4px;
}

.logo-secondary {
  color: black;
  font-size: 18.7px;
  font-family: Arial, sans-serif;
  font-weight: 400;
  line-height: 13.14px;
  text-decoration: none;
}

.logo-tracker {
  align-self: stretch;
  text-align: center;
  color: black;
  font-size: 18.7px;
  font-family: Arial, sans-serif;
  font-weight: 400;
  line-height: 13.14px;
  letter-spacing: 7.11px;
}

.navbar-user {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 24px;
}

.user-name {
  color: black;
  font-size: 20px;
  font-family: Arial, sans-serif;
  font-weight: 700;
  word-wrap: break-word;
}

.logout-btn {
  border: none;
  padding: 12px 16px;
  background: #800080;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  cursor: pointer;
}

.logout-text {
  color: white;
  font-size: 20px;
  font-family: Arial, sans-serif;
  font-weight: 400;
  word-wrap: break-word;
}
.link-btn {
  color: #800080;
  font-weight: bold;
  text-decoration: none;
  padding: 8px 16px;
  border-radius: 8px;
  border: 2px solid #800080;
  margin-left: 10px;
}

.link-btn:hover {
  background-color: #f5e6ff;
}
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: center;
    padding: 12px 16px;
    gap: 12px;
  }

  .navbar-logo {
    width: 100%;
    height: auto;
    flex-direction: row;
    justify-content: center;
    gap: 4px;
  }

  .navbar-title {
    text-align: center;
  }

  .logo-primary,
  .logo-secondary,
  .logo-tracker {
    font-size: 16px;
    line-height: normal;
  }

  .logo-tracker {
    letter-spacing: 4px;
  }

  .navbar-user {
    flex-direction: column;
    gap: 12px;
    width: 100%;
    justify-content: center;
    align-items: center;
  }

  .user-name {
    font-size: 18px;
  }

  .logout-btn {
    width: 100%;
    justify-content: center;
    padding: 10px;
  }

  .logout-text {
    font-size: 16px;
  }

  .link-btn {
    width: 100%;
    text-align: center;
    font-size: 16px;
    margin-left: 0;
  }
}
.logolink {
  display: inline-block;
  padding: 0;
  margin: 0;
  border: none;
  line-height: 0;         /* CRITICAL */
}

  
  .logo {
    display: block;          /* Removes inline space */
    max-width: 200px;
    height: auto;
    margin: 0;
    padding: 0;
    border: none;
    vertical-align: middle;  /* Try 'top' or 'bottom' too */
    line-height: 1;          /* Avoid inherited tall line-heights */
  }
  * {
    box-sizing: border-box;
  }
  
  
  